@use "sass:meta";

@import "options/root";
@import "options/break-points";
@import "bootstrap_extend/grid";

img{
    border: none !important;
}
html,body{
    overflow-x: hidden;
    min-height: 100vh;
}
img{
    border: none !important;
}
body{
    margin: 0px;
    padding: 0px;
    position: relative;
    font-family:"Helvetica Now Text";
    background-color: #fbf6f2;
    //font-size: map-get($root,'--text-size-2');
    font-weight: 300;
    font-size: 20px;
    &.locked{
        max-height: 100vh;
        height: 100vh;
        width: 100%;
        overflow: hidden;
        position: fixed;
    }
}
ul.default{
    list-style: none;
    margin: 0px;
    padding: 0px;
}
%img-responsive{
    max-width: 100%;
    width: 100%;
    height: auto;
    display: block;
}
%transition{
    -webkit-transition: all .5s; /* Safari prior 6.1 */
    transition: all .5s;
}
.form-group{
    margin-bottom:  1rem;
    margin-top: 0.5rem;
    display: flex;
    flex-direction: column-reverse;
    position: relative;
    min-height: 2.5rem;
    justify-content: flex-end;
    position: relative;
    .select__control {
        border: 2px solid rgba(88, 74, 60, 0.2);
        border-radius: 0.25rem;
        box-shadow: 0;
        min-width: 10.5rem;

    }
    .select {
        cursor: text;
        min-width: 8rem;
        padding: 0;
        display: flex;
        flex-wrap: wrap;
        height: unset;
        z-index: 1;
        overflow: hidden;
        align-items: center;
        cursor: default;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        min-height: 2.375rem;
        position: relative;
        transition: all 100ms ease 0s;
        outline: 0px;
    }
    .select__value-container {
        flex-grow: 1;
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        position: relative;
        box-sizing: border-box;
        padding: 0.125rem 0.5rem;
        overflow: hidden;
    }
    input{
        width: 100%;
        display: block;
        border: none;
        font-size: 15px;
        width: 100%;
    }
    label{
        position: absolute;
        font-weight: 600;
        transform: translateY(-8px) scale(0.857);
        position: absolute;
        top:-25px;
        left: 0.5625rem;
        transition: transform 0.2s ease-out, color 0.2s ease-out;
        cursor: text;
        padding-left: 5px;
        padding-right: 5px;
        font-size: 0.875rem;
        font-weight: 600;
        transform: translateY(13px);
        background-color: #fff;
        color: rgba(88, 74, 60, 1);
        margin-bottom: 0;
        transform-origin: 0 0;
        line-height: 1;
        letter-spacing: 0.1px;
        background-color: white;
        padding: 5px;
    }
}
.col-12{
    @extend %col-12;
}
.col-6{
    @extend %col-6;
}
.col-4{
    @extend %col-4;
}
.headline{
    height: 64px;
    background-color: #584a3c;
}
/*
p{
    line-height: 160%;
}
*/
a{
    cursor: pointer;
    color: map-get($root,'--color-3');
    text-decoration: underline;
    &:hover{
        text-decoration: none;
    }
}
.footer{
    .copyright{
        text-align: center;
        @extend %col-sm-12;
        padding-top:30px;
        padding-bottom: 30px;
        p{
            padding: 0px;
            margin: 0px;
            a{
                color: map-get($root,'--color-2');
            }
        }
    }
}
.stretch{
    align-items: stretch;
    align-items: center;
}
.btns{
    @extend %col-md-12;
    text-align: center;
}
.btn{
    margin-top: 45px;
    border: 1px solid #FFFFFF;
    color: #FFFFFF;
    display: inline-block;
    border-radius: 3px;
    padding: 19px 62px 19px 32px;
    text-decoration: none;
    text-align: center;
    font-size: 15px;
    line-height: 15px;
    letter-spacing: 0.45px;
    color: #FFFFFF;
    opacity: 1;
    transition: border .3s;
    &:hover{
        border: 1px solid #20C889;
    }
}

main{
    /*
    order: 2;
    width: 100%;
    height: calc(100vh - 4rem);
    background-color: #fbf6f1;
    box-shadow: 1px 0 5px 0 rgba(88, 74, 60, 0.16), -1px 0 8px 0 rgba(88, 74, 60, 0.12);
    position: relative;
    z-index: 1;
    overflow: auto;
    */
    @extend %col-10;
    order: 2;
}
aside{
    /*
    display: flex;
    order: 1;
    height: 100%;
    position: relative;
    overflow: auto;
    flex-shrink: 0;
    direction: rtl;
    background-color: #584a3c;
    */
    background-color: #584a3c;
    height: 100%;
    min-height: 100vh;
    @extend %col-2;
    order: 1;
}
.content{
    display: flex;
    width: 100%;
    height: calc(100vh - 4rem);
    padding: 0px 15px;
    flex-wrap: wrap;
}
.open-modaler{
    main{
        max-width: calc( (100% / 12 ) * 8.5);
        flex: 0 calc( (100% / 12 ) * 8.5);
    }
    aside{
        max-width: calc( (100% / 12 ) * 3.5);
        flex: 0 calc( (100% / 12 ) * 3.5);
    }
}

.container {
    @extend %container;
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
}
@media (min-width: 1000px) and (max-width:1200px) {
  .container {
    max-width: 100%;
    width: 100%;
    padding-left: 30px;
    padding-right: 30px;
  }
}
@media screen and (min-width:1400px) and (max-width:1680px) {
    .container{
        width: calc(100% - 5%);
        max-width: calc(100% - 5%);

        margin-left: 2.5%;
        margin-right: 2.5%;
    }

}
